.Header{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: nowrap;
    padding: 0px;
    border-bottom: 1px grey solid;
    margin: 0;
    width: 100%;
}
.Header>h1{
    margin: 8px;
}
.Icon{
    display: flex;
    flex-direction: column;
    margin: 10px;
}
.bar{
    width: 18px;
    height: 3px;
    background-color: black;
    margin: 1.3px;
}
